.aboutUs {
  .reasonsWrapper {
    width: 100%;
    height: 520px;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    .reasons {
      width: 100%;
      height: 100%;
      .reasonsTitle {
        width: fit-content;
        margin: 0px auto;
        text-transform: uppercase;
        font-size: 60px;
        font-weight: 300;
        line-height: 72px;
        color: white;
      }
      .reasonsList {
        width: 100%;
        height: 150px;
        display: flex;
        margin-top: 50px;
        justify-content: space-between;
        .reasonItem {
          color: white;
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          height: 240px;
          span {
            position: absolute;
            top: 0px;
            left: 20px;
            font-size: 96px;
            font-weight: 700;
            line-height: 115.2px;
            color: rgba(255, 153, 0, 0.2);
          }
          h6 {
            font-size: 24px;
            font-weight: 400;
            line-height: 28.8px;
            text-transform: uppercase;
          }
          p {
            height: 88px;
            margin-top: 7px;
            width: 277px;
            font-size: 18px;
            font-weight: 300;
            line-height: 21.6px;
          }
        }
      }
    }
  }
}
