.heroWrapper {
  width: 100%;
  height: 470px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  .hero {
    width: 80%;
    margin-left: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    h3 {
      margin: 0;
      font-size: 60px;
      font-weight: 300;
      display: inline;
      line-height: 72px;
      color: white;
      text-transform: uppercase;
    }
    .heroDescription {
      font-size: 20px;
      font-weight: 300;
      line-height: 24px;
      display: inline;
      margin: 0 auto;
      color: white;
      font-family: "Lato", sans-serif;
      margin-bottom: 40px;
      width: 100%;
      span {
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
      }
    }
    a {
      width: 300px;
      height: 60px;
      border: 1px solid #ff9900;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: white;
      font-size: 20px;
      font-weight: 400;
      line-height: 24px;
      text-decoration: none;
    }
  }
}
