.aboutUsWrapper {
  height: 750px;
  width: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  .aboutUs {
    height: 581px;
    width: 100%;
    display: flex;
      gap: 48px;
    .aboutUsLeft {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      &.fullWidth {
        width: 44%;
        img {
            width: 100%;
            height: 100%;
        }
      }
      .topText {
        margin: 0;
        font-size: 34px;
        font-weight: 500;
        line-height: 41px;
        letter-spacing: 0em;
        color: #ff9900;
      }
      .bottomText {
        margin: 0;
        font-size: 11px;
        font-weight: 400;
        letter-spacing: 0.09em;
        color: #ff9900;
      }
      img {
        margin-top: 40px;
      }
    }
    .aboutUsRight {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      h1 {
        margin-top: 70px;
        font-size: 50px;
        font-weight: 300;
        line-height: 72px;
        letter-spacing: 0em;
        text-transform: uppercase;
      }
      p {
        font-size: 18px;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0em;
        text-align: left;
        margin-bottom: 25px;
        span {
          font-weight: 500;
        }
        .secondSpan {
          color: #ff9900;
        }
      }
      .collections {
        display: flex;
        width: 100%;
        height: 85px;
        justify-content: space-around;
        align-items: center;
        .collection {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          p {
            margin: 0;
            font-size: 35px;
            font-weight: 700;
            line-height: 48px;
            letter-spacing: 0em;
            color: #ff9900;
          }
          span {
            margin-top: 5px;
            font-size: 18px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0em;
            text-align: left;
            color: #ff9900;
          }
        }
      }
    }
  }
}
