.footer {
    height: 149px;
    width: 100%;
    background-color: #FF9900E3;
    display: flex;
    align-items: center;
    justify-content: space-around;
    .menu {
        width: 40%;
        display: flex;
        justify-content: space-between;
        a {
            font-size: 18px;
            color: black;
            text-decoration: none;
        }
    }
    .links {
        width: 120px;
        display: flex;
        gap: 10px;
    }
}