.callSection {
    width: 100%;
    height: 830px;
    display: flex;
    .callSectionLeft {
      width: 50%;
      height: 100%;
      background-image: url("../../assets/img/Rectangle184.png");
      background-size: 100% 100%;
      background-repeat: no-repeat;
    }
    .callSectionRight {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      .callSectionRightInfo {
        margin: 0 auto;
        width: 500px;
        height: 474px;
        h4 {
          text-transform: uppercase;
          color: white;
          font-size: 40px;
          font-weight: 300;
          line-height: 48px;
        }
        input {
          width: 100%;
          height: 50px;
          background-color: transparent;
          border: none;
          border-bottom: 1px solid #ff9900;
          font-size: 16px;
          font-weight: 400;
          line-height: 19.2px;
          color: white;
          &:focus {
            outline: none;
            color: white;
          }
        }
        button {
          margin-top: 40px;
          padding: 20px 80px;
          border: 1px solid #ff9900;
          background-color: transparent;
          color: white;
        }
      }
    }
  }