.home {
  background-color: #101010;

  .hero {
    background-color: #101010;
    width: 100%;
    height: 579px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    p {
      position: relative;
      font-size: 64px;
      line-height: 83px;
      letter-spacing: 0em;
      text-align: left;
      width: 1120px;
      height: 167px;
      margin: 0;
      color: #ffffff;
      span {
        margin-left: 10px;
        position: absolute;
        background-color: #ff9900;
        width: 100%;
      }
    }
    .orderButton {
      margin-top: 20px;
      text-decoration: none;
      background-color: transparent;
      padding: 12px 40px;
      color: white;
      border: 1px solid #ff9900;
      font-size: 18px;
    }
  }
  .backgroundImg {
    height: 470px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-image: url("../../assets/img/home.png");
  }
  .aboutUsWrapper {
    height: 750px;
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    .aboutUs {
      height: 581px;
      width: 100%;
      display: flex;
      .aboutUsLeft {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .topText {
          margin: 0;
          font-size: 34px;
          font-weight: 500;
          line-height: 41px;
          letter-spacing: 0em;
          color: #ff9900;
        }
        .bottomText {
          margin: 0;
          font-size: 11px;
          font-weight: 400;
          letter-spacing: 0.09em;
          color: #ff9900;
        }
        img {
          margin-top: 40px;
        }
      }
      .aboutUsRight {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        h1 {
          margin-top: 70px;
          font-size: 50px;
          font-weight: 300;
          line-height: 72px;
          letter-spacing: 0em;
        }
        p {
          font-size: 18px;
          font-weight: 400;
          line-height: 22px;
          letter-spacing: 0em;
          text-align: left;
          span {
            font-weight: 500;
          }
          .secondSpan {
            color: #ff9900;
          }
        }
        .collections {
          display: flex;
          width: 100%;
          height: 85px;
          justify-content: space-around;
          align-items: center;
          .collection {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            p {
              margin: 0;
              font-size: 35px;
              font-weight: 700;
              line-height: 48px;
              letter-spacing: 0em;
              color: #ff9900;
            }
            span {
              margin-top: 5px;
              font-size: 18px;
              font-weight: 400;
              line-height: 22px;
              letter-spacing: 0em;
              text-align: left;
              color: #ff9900;
            }
          }
        }
      }
    }
  }
  .qualityWrapper {
    height: 750px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../../assets/img/qualityBlock.png");
    .qualityWrapperShadow {
      width: 100%;
      height: 100%;
      background-color: #1b1b1bb2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      h1 {
        margin: 0 auto;
        margin-top: 100px;
        color: white;
        font-size: 60px;
        font-weight: 300;
        line-height: 72px;
        letter-spacing: 0em;
      }
      .quality {
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: end;
        position: relative;
        .lights {
          height: 70%;
          display: flex;
          .firstRow {
            height: 100%;
            width: 25%;
            display: flex;
            flex-direction: column;
            justify-content: end;
            align-items: center;
            p {
              font-size: 18px;
              font-weight: 300;
              line-height: 22px;
              color: white;
              text-align: center;
              position: absolute;
              top: 40%;
            }
            .lamp {
              display: flex;
              flex-direction: column;
              align-items: center;
              height: 258px;
              width: 50px;
              position: absolute;
              bottom: -50px;
              justify-content: end;
              .bulp {
                box-shadow: 0px 0px 15px 5px #d9d9d9;
                background-color: white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 25px;
                height: 25px;
              }
              .stick {
                border: 1px solid white;
                height: 258px;
              }
            }
          }
          .secondRow {
            height: 100%;
            width: 50%;
            display: flex;
            position: relative;
            justify-content: center;
            align-items: center;
            .left {
              position: absolute;
              top: 0;
              left: 0;
              width: 315px;
              height: 312px;
              border-top: 1px solid white;
              border-left: 1px solid white;
              p {
                position: absolute;
                top: -85px;
                left: -45px;
                color: white;
                font-size: 18px;
                font-weight: 300;
                line-height: 22px;
                letter-spacing: 0em;
                text-align: center;
              }
              .bulp {
                position: absolute;
                top: -10px;
                left: -10px;
                box-shadow: 0px 0px 15px 5px #d9d9d9;
                background-color: white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 25px;
                height: 25px;
              }
            }
            .middle {
              box-shadow: 0px 0px 15px 5px #d9d9d9;
              background-color: white;
              border-radius: 50%;
              display: flex;
              justify-content: center;
              align-items: center;
              width: 25px;
              height: 25px;
              p {
                margin-top: 100px;
                text-align: center;
                color: white;
                position: absolute;
              }
            }
            .right {
              position: absolute;
              bottom: 0;
              right: 0;
              width: 368px;
              height: 310px;
              border-right: 1px solid white;
              border-bottom: 1px solid white;
              p {
                position: absolute;
                bottom: -100px;
                right: -70px;
                color: white;
                font-size: 18px;
                font-weight: 300;
                line-height: 22px;
                letter-spacing: 0em;
                text-align: center;
              }
              .bulp {
                position: absolute;
                bottom: -10px;
                right: -10px;
                box-shadow: 0px 0px 15px 5px #d9d9d9;
                background-color: white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 25px;
                height: 25px;
              }
            }
          }
          .thirdRow {
            height: 100%;
            width: 25%;
            display: flex;
            flex-direction: column;
            justify-content: start;
            align-items: center;
            position: relative;
            p {
              font-size: 18px;
              font-weight: 300;
              line-height: 22px;
              color: white;
              text-align: center;
              position: absolute;
              bottom: 70px;
            }
            .lamp {
              position: absolute;
              top: -100px;
              display: flex;
              flex-direction: column;
              align-items: center;
              height: 258px;
              width: 50px;
              justify-content: end;
              .bulp {
                box-shadow: 0px 0px 15px 5px #d9d9d9;
                background-color: white;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 25px;
                height: 25px;
              }
              .stick {
                border: 1px solid white;
                height: 258px;
              }
            }
          }
        }
      }
    }
  }
  .qualityBlockWrapper {
    height: 1372px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    .qualityBlock {
      display: flex;
      height: 100%;
      flex-direction: column;
      justify-content: space-between;
      .firstBlock {
        color: white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        height: 300px;
        .orangeLine1 {
          position: absolute;
          width: 50%;
          border: 1px solid #ff9900;
          bottom: 0px;
          right: -200px;
        }
        img {
          object-fit: contain;
          width: 503px;
          height: 322px;
        }
        .info {
          width: 45%;
          height: 500px;
          font-size: 25px;
          font-weight: 400;
          line-height: 30px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          h3 {
            font-size: 25px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0em;
          }
          p {
            font-size: 18px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0em;
            span {
              color: #ff9900;
            }
          }
        }
      }
      .secondBlock {
        color: white;
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: center;
        height: 300px;
        position: relative;
        .orangeLine2 {
          position: absolute;
          width: 50%;
          border: 1px solid #ff9900;
          bottom: 0px;
          left: -200px;
        }
        img {
          width: 503px;
          height: 322px;
        }
        .info {
          width: 50%;
          height: 500px;
          font-size: 25px;
          font-weight: 400;
          line-height: 30px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          h3 {
            font-size: 25px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0em;
            span {
              color: #ff9900;
            }
          }
          p {
            font-size: 18px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0em;
            span {
              color: #ff9900;
            }
          }
        }
      }
      .thirdBlock {
        color: white;
        display: flex;
        position: relative;
        justify-content: space-between;
        align-items: center;
        height: 300px;
        .orangeLine3 {
          position: absolute;
          width: 50%;
          border: 1px solid #ff9900;
          bottom: 0px;
          right: -200px;
        }
        img {
          object-fit: contain;
          width: 503px;
          height: 322px;
        }
        .info {
          width: 40%;
          height: 500px;
          font-size: 25px;
          font-weight: 400;
          line-height: 30px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          h3 {
            font-size: 25px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0em;
            span {
              color: #ff9900;
            }
          }
          p {
            font-size: 18px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0em;
          }
        }
      }
    }
  }
  .serviceWrapper {
    height: 950px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    .services {
      width: 100%;
      height: 80%;
      .title {
        width: fit-content;
        margin: 0 auto;
        h3 {
          font-size: 64px;
          font-weight: 300;
          line-height: 77px;
          letter-spacing: 0em;
          margin: 0;
        }
        p {
          text-align: center;
          font-size: 18px;
          font-weight: 300;
          line-height: 22px;
          letter-spacing: 0em;
          margin: 0;
          margin-bottom: 20px;
        }
      }
      .list {
        display: flex;
        justify-content: space-between;
        .item {
          width: 32%;
          p {
            span {
              color: #ff9900;
            }
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            letter-spacing: 0em;
          }
          img {
            width: 100%;
            height: 100%;
          }
          .img {
            width: 100%;
            height: 100%;
            background-image: url("../../assets/img/building.png");
            display: flex;
            justify-content: center;
            align-items: end;
            a {
              padding: 7px 45px 7px 45px;
              background-color: white;
              text-decoration: none;
              color: black;
              font-size: 15px;
              font-weight: 300;
              line-height: 18px;
              letter-spacing: 0em;
              margin-bottom: 30px;
              display: flex;
              justify-content: center;
              align-items: center;
              img {
                width: 8px;
                height: 14px;
                border: 1;
                margin: auto 0;
                margin-left: 10px;
              }
            }
          }
        }
      }
    }
    a {
      display: block;
      padding: 20px 65px;
      text-decoration: none;
      width: fit-content;
      margin: 0 auto;
      color: black;
      border: 1px solid #ff9900;
    }
  }
  .projectsWrapper {
    height: 1077px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .projects {
      width: 100%;
      height: 100%;
      .projectsGrid {
        display: grid;
        grid-template-columns: 2fr repeat(2, 1fr); // One big column on the left, two columns on the right
        grid-template-rows: repeat(3, 1fr); // Three rows with equal height
        gap: 10px; // Adjust the gap between grid items
        height: 80%;
        div {
          height: 100%; // Fill the height of the grid cell

          &:nth-child(1) {
            grid-column: 1 / 2;
            grid-row: span 3;
            background-image: url("../../assets/img/Rectangle184.png");
            background-size: cover;
          }

          &:nth-child(n + 2) {
            background-size: cover;
            grid-column: span 1; // Each element spans one column
            grid-row: auto; // Automatically placed in the grid
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0em;
            color: white;
          }
        }
      }
      .projectsPagination {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 20%;
        .leftArrow {
          width: 50px;
          height: 5%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        a {
          width: 300px;
          height: 60px;
          gap: 10px;
          border: 1px 0px 0px 0px;
          background-color: #ff9900;
          display: flex;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          color: black;
          font-size: 20px;
          font-weight: 400;
          line-height: 24px;
        }
        .rightArrow {
          width: 50px;
          height: 5%;
          img {
            width: 100%;
            height: 100%;
            transform: rotate(180deg);
          }
        }
      }
    }
  }
  .cooperationWrapper {
    width: 100%;
    height: 1372px;
    background-color: white;
    display: flex;
    align-items: center;
    .cooperation {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      h3 {
        width: 786px;
        font-size: 60px;
        font-weight: 300;
        line-height: 72px;
        span {
          color: #ff9900;
        }
      }
      .cooperationList {
        height: 33%;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        .cooperationListItem {
          display: flex;
          align-items: center;
          gap: 20px;
          width: 532px;
          height: 138px;
          img {
            height: 100px;
            width: 130px;
          }
          .cooperationListItemInfo {
            h6 {
              font-size: 24px;
              font-weight: 500;
              line-height: 28.8px;
              margin: 0;
              margin-bottom: 4px;
            }
            p {
              margin: 0;
              font-size: 20px;
              font-weight: 400;
              line-height: 24px;
            }
          }
        }
        .cooperationListLines {
          width: 100%;
          display: flex;
          justify-content: space-between;
          .cooperationListLine1 {
            width: 155px;
            height: 0px;
            border: 1px solid #ff9900;
            margin-left: 75px;
          }
          .cooperationListLine2 {
            width: 155px;
            height: 0px;
            border: 1px solid #ff9900;
            margin-right: 75px;
          }
        }
      }
      .cooperationBlocks {
        width: 100%;
        display: flex;
        justify-content: space-between;
        .cooperationBlock {
          width: 30%;
          img {
            width: 446px;
            height: 300px;
          }
          h6 {
            margin: 0;
            margin-top: 10px;
            font-size: 24px;
            font-weight: 500;
            line-height: 31.2px;
          }
          p {
            margin: 0;
            margin-top: 15px;
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
          }
        }
      }
    }
  }
  .callSection {
    width: 100%;
    height: 830px;
    display: flex;
    .callSectionLeft {
      width: 50%;
      height: 100%;
      background-image: url("../../assets/img/Rectangle184.png");
      background-size: 100% 100%;
      background-repeat: no-repeat;
    }
    .callSectionRight {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      .callSectionRightInfo {
        margin: 0 auto;
        width: 500px;
        height: 474px;
        h4 {
          text-transform: uppercase;
          color: white;
          font-size: 40px;
          font-weight: 300;
          line-height: 48px;
        }
        input {
          width: 100%;
          height: 50px;
          background-color: transparent;
          border: none;
          border-bottom: 1px solid #ff9900;
          font-size: 16px;
          font-weight: 400;
          line-height: 19.2px;
          color: white;
          &:focus {
            outline: none;
            color: white;
          }
        }
        button {
          margin-top: 40px;
          padding: 20px 80px;
          border: 1px solid #ff9900;
          background-color: transparent;
          color: white;
        }
      }
    }
  }
}
